import React, { useState, useEffect, useRef, useCallback, Fragment, Suspense, lazy } from 'react';

const CardFeature = (props) => {

    return (
        <Fragment>
            <div className="p-col-12 p-lg-4">
                <div className="feature-box">
                    <img src={props.imgUrl} alt="roma" />
                    <div>
                        <h3>{props.title}</h3>
                        <p>{props.content}</p>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default CardFeature;