import React, { useState, useEffect, useRef, useCallback, Fragment, Suspense, lazy } from 'react';
import { useHistory } from 'react-router-dom';
// import hook for translations and i18n configuration file
import { useTranslation } from "react-i18next";

import { Button } from 'primereact/button';

const AppFooter = () => {

    const history = useHistory();
    // destructuring t() function for useTranslation()
    const [t, i18n] = useTranslation('common');

    return (
        <Fragment>
            <div className="layout-footer-custom">
                <div className="p-grid">
                    <div className="p-col-12 p-lg-4">
                        <img src="assets/layout/images/logo-roma-white.svg" alt="logo" />
                    </div>
                    <div className="p-col-12 p-lg-4">
                        <div className="footer-text-center">
                            <h4>{t("footerPage.title1")}</h4>
                            <ul>
                                <li><Button className="p-link"><i className="pi pi-facebook"></i></Button></li>
                                <li><Button className="p-link"><i className="pi pi-github"></i></Button></li>
                            </ul>
                        </div>
                    </div>
                    <div className="p-col-12 p-lg-4">
                        <h4>{t("footerPage.title2")}</h4>
                        <ul>
                            <li><Button type="button" label={t("footerPage.subTitle1")} className="p-link" onClick={() => { history.push('/') }} /></li>
                            <li><Button type="button" label={t("footerPage.subTitle2")} className="p-link" onClick={() => { history.push('/about') }} /></li>
                        </ul>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default AppFooter;