import React, { useState, useEffect, useRef, useCallback, Fragment, Suspense, lazy } from 'react';

const CardPromotion = (props) => {

    return (
        <Fragment>
            <div className="card p-mt-3 p-mb-0">
                <h3>{props.title}</h3>
                <p>{props.content}</p>
            </div>
        </Fragment>
    );
}

export default CardPromotion;